.intro-wrap {
    @include flex(center, center, column, wrap);
    @include position-100;
    position: fixed;
    width: 100%;
    height: 100%; 
    z-index: 3; 
    background-color: #000; 
    pointer-events: none;

    .intro-logo {
        @include font(50px, 800, 1);
        margin-bottom: 10px;
    }
    h1 {
        @include font(10px, 300, 1);
    }
    
    .loading-bar {
        width: calc(100% - 32px);
        max-width: 375px;
        height: 6px;
        border-radius: 50px;
        overflow: hidden;
        border: 0.5px solid $mainColor;
        box-sizing: border-box;
        position: absolute;
        bottom: 100px;
        left: 50%;
        transform: translateX(-50%);

        &::after {
            content: "";
            display: block;
            width: 0%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-color: $mainColor;
        }

        &.on::after {
            animation: loading 2s 1 forwards;
        }

        @keyframes loading {
            0% {width: 0;}
            100% {width: 100%;}
        }
    }
}