.account-wrap {
    @include flex(center, space-between, column, wrap);
    width: 100%;
    height: 100%;
    padding: 70px 0;
    box-sizing: border-box;
    text-align: center;
    opacity: 0;
    background-color: #000;

    .guest-auth {
        @include position-center();
        @include font(16px, 500, 1.5);
        color: $gray2;
        top: 40%;
    }

    .top-area {
        .date {
            @include font(20px, 600, 1);
            margin-bottom: 6px;
        }

        .time {
            @include font(100px, 800, 1);
        }
    }

    .bot-area {
        @include flex(center, center, column, wrap);
        @include calc-width();
        max-width: 400px;
        position: relative;
        z-index: 1;

        .account-btn {
            @include flex(center, center, row, wrap);
            column-gap: 10px;

            button {
                @include flex(center, center, column, wrap);
                background-color: $mainColor;
                color: #fff;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                overflow: hidden;

                svg {
                    width: 50%;
                    height: 50%;
                }
            }
        }
    }

    .login-join-form {
        @include flex(center, center, column, wrap);
        width: 100%;

        form {
            @include flex(center, center, column, wrap);
            row-gap: 10px;
            margin-top: 30px;
            width: 100%;
    
            input, button {
                width: 100%;
                height: 36px;
                padding: 0 10px;
                border-radius: 6px;
                border: 0;
                border-bottom: 1px solid #000;
                box-sizing: border-box;
                background-color: $mainColor;
                color: #fff;
            }
        }

        .github-btn {
            @include flex(center, center, row, wrap);
            column-gap: 5px;

            width: 100%;
            height: 36px;
            padding: 0 10px;
            margin-top: 10px;
            border-radius: 6px;
            border: 0;
            border-bottom: 1px solid #000;
            box-sizing: border-box;
            background-color: $mainColor;
            color: #fff;
        }

        .error-txt {
            @include font(12px, 400, 1);
            color: $red;
            text-align: center;
            margin-top: 15px;
            word-break: keep-all;
        }
    }
}

@media all and (max-width: $bp_MO){
    .account-wrap {
        padding: 50px 0;
    }
}