.kty-wrap {
    width: 100%;

    .main-sec {
        @include flex(center, center, column, wrap);
        //@include position-100();
        top: 40px;
        width: 100%;
        height: calc(100vh - 75px);
        box-sizing: border-box;
        background-color: #fff;
        z-index: 9;
        scroll-margin-top: 75px;

        .inner {
            text-align: center;

            h2 {
                @include font(46px, 900, 1.5);
            }

            .img {
                height: 0px;
                margin-top: 30px;
                animation: kty-main-Hscale 0.7s 1.7s cubic-bezier(0, 0.55, 0.45, 1) 1 forwards;
                overflow: hidden;

                img {
                    height: 300px;
                }
            }
        }
    }

    nav {
        position: fixed;
        top: 95px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 10;
        width: calc(100% - 32px);
        max-width: 500px;
        height: 60px;
        padding: 8px 10px;
        box-sizing: border-box;
        border-radius: 50px;
        background-color: rgba(0, 0, 0, 0.4);
        backdrop-filter: blur(50px);
        border: 1px solid #000;
        box-shadow: 0 0 10px #000;
        opacity: 0;
        animation: kty-nav 0.5s 2.5s ease-in 1 forwards;

        ul {
            @include flex(center, center, row, nowrap);
            width: 100%;
            height: 100%;
            position: relative;

            &::before {
                content: "";
                display: block;
                width: calc(100% / 4);
                height: 100%;
                background-color: $mainColor;
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                z-index: 2;
                border-radius: 50px;
                transition: 0.5s;
            }

            &.main-nav::before {
                left: 0;
            }
            &.sec1-nav::before {
                left: calc(100% / 4);
            }
            &.sec2-nav::before {
                left: calc((100% / 4) * 2);
            }
            &.sec3-nav::before {
                left: calc((100% / 4) * 3);
            }
            &.no::before {
                background-color: transparent;
            }

            li {
                width: calc(100% / 4);
                height: 100%;
                @include flex(center, center, column, wrap);
                @include font(18px, 600, 1);
                color: #fff;
                width: 100%;
                height: 100%;
                text-align: center;
                position: relative;
                z-index: 3;
                cursor: pointer;
            }
        }
    }

    .sec {
        position: relative;
        scroll-margin-top: 75px;
        display: none;

        &.displayblock {
            display: block;
        }

        h4 {
            @include font(50px, 900, 1);
        }

        &1 {
            height: calc((100vh - 75px) * 4);
            background-color: $gray;

            .scroll-zone {
                width: 100%;
                height: calc(100% / 4);
                position: sticky;
                top: 75px;

                .con {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: -1;
                    opacity: 0;
                    // transition: opacity 0.01s;

                    p {
                        @include position-center();
                        @include font(0, 800, 1.5);
                        width: calc(100% - 32px);
                        text-align: center;
                        transform: translate(-50%, 100px);

                        span {
                            font-size: 60px;
                        }
                    }

                    &.on {
                        z-index: 1;
                        opacity: 1;
                        transition: opacity 0.5s;
                        p {
                            animation: kty-sec1-txtSize 0.5s 0.8s 1 forwards;
                            transform: translate(-50%, -50%);
                            transition: 0.5s;
                        }
                    }
                }
            }
        }

        &2 {
            padding: 150px 0;

            .profile {
                @include flex(center, center, row, nowrap);
                width: 100%;
                column-gap: 50px;
                max-width: 1000px;
                margin: 0 auto;

                .img-box {
                    width: 100%;
                    max-width: 300px;
                }

                .txt-box {
                    width: calc(100% - 350px);
                    word-break: keep-all;

                    h3 {
                        @include font(26px, 900, 1.5);
                        margin-bottom: 30px;
                    }

                    p {
                        @include font(18px, 400, 1.5);
                    }
                }
            }
        }

        &3 {
            padding: 150px 0;
            background-color: $gray;
            color: #000;
            transition: 1s;

            &.on {
                background-color: $mainColor;
                color: #fff;
            }

            .inner {
                & > div {
                    width: 100%;
                    max-width: 650px;
                    margin: 0 auto;

                    &:not(:first-child) {
                        margin-top: 100px;
                    }
                }
            }

            ul {
                margin-top: 50px;

                li {
                    @include font(20px, 500, 1.3);
                    word-break: keep-all;

                    &:not(:first-child) {
                        margin-top: 20px;
                    }
                }
            }
        }

        &4 {
            width: 100%;
            height: calc(100vh - 75px);
            box-sizing: border-box;
            text-align: center;

            .inner {
                @include flex(center, center, column, wrap);
                height: 100%;

                p {
                    @include font(46px, 900, 1.5);

                    &::after {
                        content: ":)";
                        display: inline-block;
                        margin-left: 10px;
                        transform: translateY(-2px);
                        animation: kty-txt 1.5s infinite;
                    }
                }
            }
        }
    }
}

@keyframes kty-txt {
    0% {content: ":)";}
    100% {content: ";)";}
}

@keyframes kty-main-Hscale {
    0% {height: 0;}
    100% {height: 300px;}
}

@keyframes kty-nav {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@keyframes kty-sec1-txtSize {
    0% {font-size: 0;}
    100% {font-size: 60px;}
}

@media all and (max-width: 800px){
    .kty-wrap {
        .sec {
            &2 {
                .profile {
                    flex-direction: column;
                    column-gap: 0px;
                    row-gap: 50px;
    
                    .txt-box {
                        width: 100%;
                        text-align: center;
                    }
                }
            }
            
        }
    }
}

@media all and (max-width: $bp_MO){
    .kty-wrap {
        width: 100%;
    
        .main-sec {
            height: calc((var(--vh, 1vh) * 100) - 70px);
            scroll-margin-top: 70px;
    
            .inner {
    
                h2 {
                    font-size: 30px;
                }
    
                .img {
                    margin-top: 20px;
    
                    img {
                        height: 200px;
                    }
                }

                @keyframes kty-main-Hscale {
                    0% {height: 0;}
                    100% {height: 200px;}
                }
            }
        }

        nav {
            top: 90px;
            height: 50px;
    
            ul {
                li {
                    width: calc(100% / 4);
                    height: 100%;
                    font-size: 14px;
                }
            }
        }

        .sec {
            scroll-margin-top: 70px;
    
            h3 {
                font-size: 40px;
            }
    
            &1 {
                height: calc(((var(--vh, 1vh) * 100) - 70px) * 4);
    
                .scroll-zone {
                    top: 70px;
    
                    .con {
                        p {
                            @keyframes kty-sec1-txtSize {
                                0% {font-size: 0;}
                                100% {font-size: 30px;}
                            }
    
                            span {
                                font-size: 30px;
                            }
                        }
                    }
                }
            }

            &2 {
                padding: 100px 0;

                .profile {
                    .img-box {
                        max-width: 200px;
                    }

                    .txt-box {
                        h3 {
                            font-size: 30px;
                        }

                        p {
                            font-size: 13px;
                        }
                    }
                }
            }
    
            &3 {
                padding: 100px 0;
    
                .inner {
                    & > div {
                        &:not(:first-child) {
                            margin-top: 80px;
                        }
                    }
                }
    
                ul {
                    margin-top: 30px;
    
                    li {
                        font-size: 14px;
    
                        &:not(:first-child) {
                            margin-top: 15px;
                        }
                    }
                }
            }
    
            &4 {
                height: calc(100vh - 70px);
    
                .inner {
                    p {
                        font-size: 30px;
    
                        &::after {
                            content: ":)";
                            display: inline-block;
                            margin-left: 10px;
                            transform: translateY(-2px);
                            animation: kty-txt 1.5s infinite;
                        }
                    }
                }
            }
        }
    }
}