.game-wrap {
    height: calc(100vh - 75px);
    padding-bottom: 100px;
    box-sizing: border-box;

    .game-box {
        @include flex(center, flex-start, column, nowrap);
        width: calc(100% - 32px);
        padding-top: 150px;
        box-sizing: border-box;
        max-width: 500px;
        height: 100%;
        margin: 0 auto;
        overflow: hidden;
        overflow-y: scroll;
        
        .game-tit {
            @include font(28px, 800, 1);
            margin-bottom: 20px;
            text-align: center;
        }
    }

    .game- {
        &word, &giftDraw {
            width: 100%;
            padding: 30px;
            border-radius: 12px;
            box-sizing: border-box;
            background-color: $gray;
            text-align: center;
        }

        &word {
            .quiz-Q {
                word-break: keep-all;

                p {
                    @include font(18px, 400, 1.5);
                    margin-bottom: 10px;
                }

                span {
                    @include font(15px, 400, 1.3);
                    color: $gray2;
                }
            }

            .quiz-A {
                @include flex(center, center, row, wrap);
                column-gap: 5px;
                row-gap: 5px;
                width: 100%;
                margin: 50px auto 30px;

                input {
                    width: 80px;
                    height: 80px;
                    border-radius: 20px;
                    border: 1px solid #333;
                    text-align: center;
                    font-size: 36px;
                    cursor: pointer;
                    text-transform: uppercase;

                    &::placeholder {
                        color: #eee;
                    }
                }
            }

            .A-btn {
                @include font(18px, 800, 50px);
                width: 100%;
                max-width: 250px;
                height: 50px;
                background-color: $gray2;
                cursor: pointer;
                color: #fff;
                border-radius: 10px;
                margin: 0 auto;
            }

            /* 정답일 때 모션 */
            .yes-A {
                animation: A_yes 0.3s ease-in-out 1 forwards;
            }
            @keyframes A_yes {
                0% {background-color: transparent; color:#000;}
                100% {background-color: rgb(55, 55, 55); color:#fff;}
            }

            /* 정답 아닐 때 모션 */
            .no-A {
                animation: A_no 0.5s 1 forwards;
            }
            @keyframes A_no {
                0% {transform: rotate(-1deg) scale(1);}
                20% {transform: rotate(0deg);}
                40% {transform: rotate(1deg) scale(1.1);}
                60% {transform: rotate(0deg) ;}
                80% {transform: rotate(-1deg) scale(1);}
                100% {transform: rotate(0deg);}
            }

            .result-text {
                @include font(16px, 600, 1);
                margin-top: 20px;
            }
        }

        &giftDraw {
            p {
                @include font(16px, 600, 1);
                margin-bottom: 20px;
            }
            ul {
                margin-bottom: 30px;
                li {
                    @include font(14px, 400, 1);

                    &:not(:first-child) {
                        margin-top: 10px;
                        
                    }
                }
            }
            .gift-boxes {
                margin: 50px auto 30px;
                @include flex(center, center, row, wrap);
                column-gap: 30px;
                row-gap: 30px;

                svg {
                    width: 60px;
                    height: 60px;
                    color: #333;
                }
            }
            .result-show {
                @include flex(center, center, column, wrap);
                width: 100%;
                img {
                    width: 30%;
                    margin: 0 auto;
                }
                button {
                    @include font(16px, 600, 1);
                    padding: 10px 20px;
                    background-color: $gray2;
                    cursor: pointer;
                    color: #fff;
                    border-radius: 10px;
                    margin-top: 20px;
                }
            }
        }
    }
}

@media all and (max-width: $bp_MO){
    .game-wrap {
        height: calc((var(--vh, 1vh) * 100) - 70px);
        padding-bottom: 98px;

        .game-box {
            padding-top: 50px;
        }

        .game- {
            &word {
                .quiz-Q {
                    p {
                        font-size: 14px;
                    }
                    span {
                        font-size: 12px;
                    }
                }
    
                .quiz-A {
                    margin: 30px auto;
    
                    input {
                        width: 50px;
                        height: 50px;
                        font-size: 30px;
                    }
                }
    
                .result-text {
                    font-size: 14px;
                }
            }

            &giftDraw {
                p {
                    font-size: 15px;
                    margin-bottom: 12px;
                }
                ul {
                    margin-bottom: 20px;
                    li {
                        font-size: 12px;
    
                        &:not(:first-child) {
                            margin-top: 6px;
                            
                        }
                    }
                }
                .gift-boxes {
                    margin: 30px auto 10px;
                    column-gap: 20px;
                    row-gap: 20px;
    
                    svg {
                        width: 40px;
                        height: 40px;
                    }
                }
                .result-show {
                    img {
                        width: 35%;
                    }
                }
            }
        }
    }
}