@mixin workpage-width() {
    width: calc(100% - 32px);
    margin: 0 auto;
    padding-left: 16px;
    padding-right: 16px;
}

@mixin workpage-padding($padding) {
    padding-left: $padding;
    padding-right: $padding;
}

.work-wrap {
    background-color: $gray;
    padding-top: 100px;

    .head {
        @include flex(flex-end, space-between, row, wrap);
        @include workpage-width();
        @include workpage-padding(16px);
        padding-top: 20px;
        padding-bottom: 12px;
        border-bottom: 1px solid #000;
        background-color: $gray;
        box-sizing: border-box;
        position: sticky;
        top: 75px;
        z-index: 10;

        p {
            @include font(40px, 800, 1.5);
        }

        span {
            @include font(18px, 600, 1.5);
        }
    }

    .work-list {
        @include flex(flex-start, flex-end, column, nowrap);
        @include workpage-width();
        @include workpage-padding(16px);
        padding-bottom: 150px;
        box-sizing: border-box;
        height: calc(100vh - 268px);

        ul {
            li {
                @include flex(flex-start, flex-start, row, nowrap);
                column-gap: 10px;
                width: fit-content;
                cursor: pointer;

                &:not(:last-child) {
                    margin-bottom: 10px;
                }

                &::before {
                    content: "";
                    display: block;
                    width: 5px;
                    height: 5px;
                    border-radius: 50%;
                    background-color: $mainColor;
                    transform: translateY(10px);
                }

                p {
                    @include font(18px, 500, 1.5);
                    width: calc(100% - 15px);
                    position: relative;
                    color: $mainColor;

                    &::after {
                        content: '';
                        display: block;
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        width: 100%;
                        height: 2px;
                        background-color: $mainColor;
                        transform-origin: 0 0;
                        transform: scaleX(0);
                        transition-duration: 0.2s;
                    }

                    &:hover::after {
                        transform: scaleX(1);
                    }
                }
            }
        }
    }

    .sec {
        @include workpage-width();
        @include workpage-padding(16px);
        position: relative;
        padding-bottom: 45vh;
        box-sizing: border-box;
        overflow: hidden;

        .img-box {
            @include flex(flex-start, flex-start, row, wrap);
            column-gap: 10px;
            row-gap: 10px;
            margin-bottom: 50px;
            
            img {
                height: 250px;
            }
        }

        .work-summary {
            @include font(18px, 500, 1.5);
            word-break: keep-all;
            width: 100%;
            max-width: 1000px;
        }

        a {
            @include font(18px, 500, 1.5);
            color: $mainColor;
            border-bottom: 1px solid $mainColor;
            box-sizing: border-box;
        }
    }

    .sec1 {
        .img-box img {
            height: 500px;
        }
    }

    .sec3 a {
        margin-top: 30px;
    }
}

@media all and (max-width: $bp_MO){
    .work-wrap {
        padding-top: 50px;

        .head {
            top: 70px;
            @include workpage-padding(8px);

            p {
                font-size: 20px;
                width: 100%;
            }

            span {
                font-size: 14px;
            }
        }

        .work-list {
            @include workpage-padding(8px);
            height: calc((var(--vh, 1vh) * 100) - 204px);
            padding-bottom: 100px;
            
            ul li a {
                font-size: 16px;
            }
        }

        .sec {
            @include workpage-padding(8px);
            padding-bottom: 150px;

            .img-box img {
                height: auto;
                width: 80%;
            }

            .work-summary {
                font-size: 14px;
            }

            a {
                font-size: 14px;
            }
        }

        .sec1 {
            .img-box img {
                height: auto;
            }
        }
    }
}