.file-wrap {
    @include flex(center, center, column, wrap);
    row-gap: 6px;

    .file {
        width: 50px;
        height: 50px;
        border-radius: 8px;
        background-color: $gray;
        position: relative;
        
        &::after {
            content: "";
            width: 100%;
            height: 15px;
            border-radius: 6px 6px 0 0;
            background-color: $mainColor;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
        }
    }
    
    p {
        @include font(10px, 400, 1.5);
        color: #fff;
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        word-break: break-all;
    }
}