html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center,
dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary,time, mark, audio, video 
{margin: 0; padding: 0; border: 0; font: inherit;}
body {overscroll-behavior-y: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;}
ol, ul {list-style: none;}
a {text-decoration: none; color: #000; display: inline-block;}
button {background: inherit ; border:none; box-shadow:none; border-radius:0; padding:0; overflow:visible; cursor:pointer}
input, input:focus, input:focus-visible {outline: 0;}
svg {vertical-align: middle;}

// 변수, mixin 모음 파일
$mainColor: #535150;
$subColor: #EBEBE3;
$black: #000;
$white: #fff;
$gray: #ccc;
$gray2: #8b8b8b;
$red: #ec4141;
$green: #4caf50;

$grid1: 100%;
$grid2: 50%;
$grid3: 33.3333%;
$grid4: 25%;

$bp_MO: 489px;

:root {
    --vh: 100%;
}

@mixin flex($align, $justify, $direction, $wrap) {
    display: flex;
    align-items: $align;
    justify-content: $justify;
    flex-direction: $direction;
    flex-wrap: $wrap;
}

@mixin font($size, $weight, $lineHeight) {
    font-size: $size;
    font-weight: $weight;
    line-height: $lineHeight;
}

@mixin bg-img($size, $position, $repeat){
    background-size: $size;
    background-position: $position;
    background-repeat: $repeat;
}

@mixin position-center() {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@mixin position-100() {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

@mixin calc-width() {
    width: calc(100% - 32px);
    margin: 0 auto;
}

.displaynone {
    display: none !important;
}

.fadeout {
    animation: fadeout 0.2s 1 forwards;
}
@keyframes fadeout {
    0% {opacity: 1;}
    100% {opacity: 0;}
}

.fadein {
    animation: fadein 0.2s 1 forwards;
}
@keyframes fadein {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

.w100 {
    width: 100%;
}