.folder-wrap {
    width: 50px;
    height: fit-content;
    text-align: center;
    padding-top: 7px;
    position: relative;
    @include flex(center, center, column, wrap);
    row-gap: 6px;

    .folder-top {
        width: 100%;
        height: 10px; 
        display: block;
        background: #21aae6;
        position: absolute;
        top: 3px;
        left: 0;
        z-index: 1;
        border-radius: 0px 5px 0 0;

        &::before {
            width: 40%;
            height: 10px;
            content: "";
            display: block;
            background: #21aae6;
            position: absolute;
            top: -3px;
            left: 0;
            border-radius: 3px 10px 0px 0;
        }
    }

    .folder {
        width: 100%;
        height: 35px;
        border-radius: 5px;
        background: rgb(33,170,230);
        background: linear-gradient(0deg, rgba(33,170,230,1) 0%, rgba(96,209,255,1) 10%, rgba(96,209,255,1) 100%);
        box-shadow: 0px 0px 6px #000000b0;
        position: relative;
        z-index: 2;
    }

    p {
        font-size: 12px;
        font-weight: 400;
    }
}