.popup-wrap {
    position: absolute;
    left: 80px;
    width: 60%;
    height: 60%;
    max-width: 800px;
    max-height: 500px;
    border-radius: 10px;
    background-color: #1f2123;
    z-index: 999999 !important;
    border: 1px solid #464646;
    box-sizing: border-box;
    box-shadow: 2px 2px 10px #000;
    overflow: hidden;
    cursor: auto !important;

    &.full {
        width: 100%;
        height: 100%;
        max-width: none;
        max-height: none;
        transform: translate(0, 0) !important;
        left: 0 !important;
    }
}
.popup- {
    &head {
        @include flex(center, flex-start, row, wrap);
        column-gap: 6px;
        width: 100%;
        height: 40px;
        padding: 0px 16px;
        box-sizing: border-box;
        background-color: #353536;
        border-radius: 10px 10px 0 0;
        z-index: 10;

        & > div {
            width: 10px;
            height: 10px;
            border-radius: 50%;
        }

        & > p {
            @include font(15px, 500, 1);
            margin-left: 20px;
        }
    }

    &close {
        background-color: #ff5f57;
    }

    &hidden {
        // background-color: #febc2e;
        background-color: #414141;
    }

    &size {
        background-color: #28c840;
    }

    &body {
        width: 100%;
        height: calc(100% - 40px);
        padding: 16px;
        box-sizing: border-box;

        .file-list {
            @include flex(center, flex-start, row, wrap);
            column-gap: 16px;
            row-gap: 16px;
            width: 100%;

            li {
                text-align: center;
                position: relative;
                width: 50px;
            }
            
        }
    }
}
.browser {
    position: relative;
    z-index: 10;
    padding: 40px 0 0;

    &-head {
        position: fixed;
        top: 35px;
        left: 0;

        .popup-hidden, .popup-size {
            background-color: #414141;
        }
    }

    .browser-body {
        background-color: #fff;
        color: #242424;
    }
}

@media all and (max-width: $bp_MO){
    .popup-wrap {
        width: 100%;
        height: 100%;
        max-width: none;
        max-height: none;
        transform: translate(0, 0) !important;
        left: 0 !important;
    }

    .popup-size {
        background-color: #414141;
    }

    .browser {
        padding: 40px 0 0;

        &-head {
            top: 30px;
            height: 40px;
        }
        
    }
}