body {
    background-color: #000;
    color: #fff;
}

#wrap {
    width: 100%;
    height: 100vh;
    position: relative;

    .bg-txt {
        @include font(18px, 800, 1.5);
        @include position-center();
        z-index: 1;
        display: block;
        opacity: 0.5;
        pointer-events: none;
        text-align: center;
    }

    #header {
        @include flex(center, space-between, row, wrap);
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 99;
        width: 100%;
        height: 35px;
        padding: 0 20px;
        box-sizing: border-box;
        background-color: rgba(0, 0, 0, 0.4);
        backdrop-filter: blur(50px);

        ul {
            display: flex;
            align-items: center;
            column-gap: 20px;

            li {
                @include font(14px, 500, 1);

                &.diff {
                    font-weight: 800;
                }

                &.log-out {
                    cursor: pointer;
                }

                a {
                    font-size: inherit;
                    font-weight: inherit;
                    color: #fff;
                }

                &.clock {
                    @include flex(center, center, row, wrap);
                    column-gap: 5px;
                }
            }
        }
    }

    .container {
        width: 100%;
        height: 100%;
        padding: 35px 0 100px;
        box-sizing: border-box;
        position: relative;
        // background: #d9e5f1 url("../img/layout-bg.jpeg") no-repeat center / contain;
        // background: url("https://images.unsplash.com/photo-1617128734662-66da6c1d3505?crop=entropy&cs=tinysrgb&fit=max&fm=jpg&ixid=MnwxNDU4OXwwfDF8cmFuZG9tfHx8fHx8fHx8MTYxNzc3NTM3MA&ixlib=rb-1.2.1&q=75&w=1920") no-repeat center / cover;
        background-color: $mainColor;
    }

    .inner {
        width: calc(100% - 32px);
        max-width: 1280px;
        margin: 0 auto;
    }

    #FNB {
        position: fixed;
        bottom: 16px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 99;
        width: max-content;
        border-radius: 16px;
        padding: 8px;
        box-sizing: border-box;
        background-color: rgba(0, 0, 0, 0.4);
        backdrop-filter: blur(50px);
        border: 1px solid #000;
        box-shadow: 0 0 10px #000;

        ul {
            @include flex(center, space-around, row, wrap);
            width: 100%;
            column-gap: 8px;
            row-gap: 8px;

            li {
                width: 50px;
                height: 50px;
                border-radius: 16px;
                background-color: $mainColor;
                cursor: pointer;

                a {
                    @include flex(center, center, row, wrap);
                    width: 100%;
                    height: 100%;
                    color: #fff;

                    svg {
                        width: 20px;
                        height: 20px;
                    }
                }
            }
        }
    }
}

// 에러 페이지 + 로딩
.error-404, .loading-wrap {
    @include position-center();
    @include font(30px, 900, 1);
}

@media all and (max-width: $bp_MO){
    #wrap {
        height: calc(var(--vh, 1vh) * 100);
        .container {
            padding: 30px 0 98px;
        }

        #header {
            height: 30px;
            padding: 0 16px;
    
            ul{
                li {
                    font-size: 12px;
                }
            }
        }
    }
}